@import url(fonts/ZenKakuGothicNew-Medium/style.css);
@import url(fonts/Chalkboard/stylesheet.css);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-boxw;
}

body {
  background-color: #ffffff;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  background: transparent;
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background: #d00064;
  border-radius: 100px;
  transition: 1s;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(#d00064, #43064a);
}

::-webkit-scrollbar-button {
  background: transparent;
}
